@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,400;0,700;1,400&display=swap);


.poster {
  background: no-repeat center var(--gradient-background);
  height: 100vh;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-sizing: border-box;
}

.poster-h1 {
  font-size: calc(4em + 1vw);
}

.poster-h2 {
  font-size: calc(2em + 1vw);
}
.startArrow {
  cursor: pointer;
  position: absolute;
  -webkit-animation: vertical 2.5s infinite;
          animation: vertical 2.5s infinite;
}

@-webkit-keyframes vertical {
  0% {
    top: 70vh;
  }
  50% {
    top: 75vh;
  }
  100% {
    top: 70vh;
  }
}

@keyframes vertical {
  0% {
    top: 70vh;
  }
  50% {
    top: 75vh;
  }
  100% {
    top: 70vh;
  }
}

.menu-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: #a06b8a;
  position: fixed;
  width: 100vw;
  box-shadow: 0px 6px 26px -8px rgba(0, 0, 0, 0.75);
  z-index: 999;
  font-size: calc(0.7rem + 1vw);
}

.menu-container a {
  color: #ffffff;
}

@media all and (min-width: 768px) {
  .menu-container {
    font-size: calc(1rem + 1vw);
  }
}

.about-me {
  color: var(--main-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 51px;
}

.about-me-h2,
.about-me-p {
  padding: 1rem;
  text-align: center;
}

.about-me-h2 {
  text-transform: uppercase;
}

.about-me-foto {
  border-radius: 50%;
  width: calc(20em + 1vw);
}

.knowledge {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.knowledge-circle {
  color: white;
  height: 6rem;
  width: 6rem;
  border-radius: 50%;
  background-color: var(--main-color);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 1rem;
  margin: 0.5rem;
}

@media all and (min-width: 768px) {
  .about-me {
    flex-direction: row;
  }

  .about-me-p {
    text-align: center;
    font-size: 1.5rem;
  }

  .about-me-foto {
    align-self: flex-start;
  }
}

.main-project-card {
  border: 1px solid rgba(255, 121, 72, 1);
  text-align: center;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-project-card-title,
.main-project-card-language {
  margin-bottom: 0.5rem;
}

.main-project-card-link {
  color: rgba(255, 121, 72, 1);
  font-size: medium;
  font-size: initial;
}

.main-project-card-link:hover {
  font-size: larger;
}

@media all and (min-width: 768px) {
  .main-project-card {
    font-size: 1.25rem;
  }

  .main-project-card-link {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.main-proyects-container {
  color: var(--main-color);
  display: flex;
  justify-content: center;
  padding-top: 51px;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 10px;
}

.main-proyects-title {
  font-size: x-large;
  width: 20vw;
  display: flex;
  align-items: center;
  -webkit-writing-mode: vertical-lr;
      -ms-writing-mode: tb-lr;
          writing-mode: vertical-lr;
  -webkit-text-orientation: upright;
          text-orientation: upright;
  text-transform: uppercase;
}

@media all and (min-width: 768px) {
  .projects-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

.hidden {
  display: none;
}

.card-riht-div {
  margin-bottom: 5px;
  -webkit-animation-name: RightCard;
          animation-name: RightCard;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;

  opacity: 0.7;
  border-right: 8px solid rgba(255, 121, 72, 1);
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  text-align: right;
  max-width: 50%;
}

@-webkit-keyframes RightCard {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    transform: translateX(calc(-0vw + 8px));
  }
}

@keyframes RightCard {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    transform: translateX(calc(-0vw + 8px));
  }
}

.hidden {
  display: none;
}

