  margin-bottom: 5px;
  animation-name: LeftCard;
  animation-duration: 3s;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
} */

.card-left-div {
  margin-bottom: 5px;
  -webkit-animation-name: LeftCard;
          animation-name: LeftCard;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;

  opacity: 0.7;
  border-left: 8px solid rgba(255, 121, 72, 1);
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  max-width: 50%;
}

@-webkit-keyframes LeftCard {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    transform: translateX(50vw);
  }
}

@keyframes LeftCard {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    transform: translateX(50vw);
  }
}

.experience {
  display: flex;
  flex-direction: column;
  background-color: var(--background-color);
  color: var(--main-color);
  padding-top: 51px;
}

.experience-h2 {
  text-align: center;
  text-transform: uppercase;
  padding: 1rem;
}

.download-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.banner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 2px solid rgba(255, 121, 72, 1);
  border-bottom: 2px solid rgba(255, 121, 72, 1);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1rem;
}

.download-h2 {
  padding-bottom: 1rem;
  font-size: xx-large;
  color: var(--main-color);
  text-align: center;
}

.contact-container {
  padding-top: 1rem;
  color: var(--main-color);
  padding-top: 51px;
}

.contact-h2 {
  padding-bottom: 1rem;
  border-bottom: rgba(255, 121, 72, 1) 1px solid;
  font-size: xx-large;
}

.contact-icons-container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 1rem;
}

.contact-icon {
  width: 50px;
}

.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  color: #ffff;
  background-color: rgb(0, 0, 0);
}

small {
  padding: 1rem;
}

* {
  --main-font: 'Mulish', sans-serif;
  --main-color: #47167d;
  --background-color: #f6efef;
  --gradient-background: linear-gradient(299deg, rgba(121, 0, 255, 1) 20%, rgba(255, 121, 72, 1) 80%);
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-family: var(--main-font);
}

a,
button {
  cursor: pointer;
  text-decoration: none;
}

section {
  padding: 1rem;
  background-color: var(--background-color);
}

.hide {
  display: none;
}

.view {
  display: unset;
}

.main-container {
  padding-top: 51px;
}



